.vue-drag-uploader {
  display: flex;
  flex-wrap: wrap;
  padding: 15px 5px 5px 15px;
  border: 2px dashed #999;
  border-radius: 4px;
  cursor: pointer;
  color: #999;
  min-height: 250px; }
  .vue-drag-uploader--readonly {
    border: 1px solid #999; }
  .vue-drag-uploader--ondrag {
    border: 2px solid #666;
    background-color: rgba(0, 0, 0, 0.05);
    color: #666; }
  .vue-drag-uploader__draggable-wrapper, .vue-drag-uploader__transition-wrapper {
    display: flex;
    flex-wrap: wrap; }
  .vue-drag-uploader__item {
    width: 155px;
    height: 155px;
    border: 1px solid rgba(0, 0, 0, 0.25);
    border-radius: 3px;
    cursor: pointer;
    margin-right: 15px;
    margin-bottom: 15px; }
  .vue-drag-uploader .add-button {
    display: flex;
    align-items: center;
    text-align: center;
    transition: background-color .5s; }
    .vue-drag-uploader .add-button:hover {
      background-color: rgba(0, 0, 0, 0.05); }
    .vue-drag-uploader .add-button__body {
      margin: 0 auto; }
    .vue-drag-uploader .add-button__icon {
      margin-bottom: 10px; }
    .vue-drag-uploader .add-button__text {
      font-size: 14px; }
  .vue-drag-uploader .preview-img {
    position: relative;
    cursor: pointer; }
    .vue-drag-uploader .preview-img > * {
      position: absolute; }
    .vue-drag-uploader .preview-img:hover .error-message__message {
      display: block;
      padding: 10px; }
    .vue-drag-uploader .preview-img__body {
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: transparent no-repeat center center;
      background-size: cover; }
    .vue-drag-uploader .preview-img__title {
      margin: 0 auto; }
    .vue-drag-uploader .preview-img__overlay {
      display: flex;
      align-items: center;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      background-color: rgba(0, 0, 0, 0.35);
      opacity: 0;
      transition: opacity .5s; }
      .vue-drag-uploader .preview-img__overlay:hover {
        opacity: 1; }
    .vue-drag-uploader .preview-img__remove-icon {
      position: absolute;
      top: 5px;
      right: 5px;
      color: white;
      opacity: .75;
      transition: opacity .5s; }
      .vue-drag-uploader .preview-img__remove-icon:hover {
        opacity: 1; }
    .vue-drag-uploader .preview-img__progress {
      height: 3px;
      background-color: rgba(255, 255, 255, 0.5);
      width: 100%;
      bottom: 0;
      left: 0; }
    .vue-drag-uploader .preview-img__progress-bar {
      background-color: #007bff;
      width: 0;
      height: 100%; }
  .vue-drag-uploader .error-message {
    width: 100%;
    background-color: #dc3545;
    color: white;
    font-size: 14px;
    word-break: break-all;
    top: calc(100% - 26px);
    min-height: 26px; }
    .vue-drag-uploader .error-message__notice {
      display: inline-block;
      padding: 3px;
      text-align: center;
      width: 100%; }
    .vue-drag-uploader .error-message__message {
      display: none; }
